@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300&display=swap);
@font-face {
    font-family: 'Montserrat-Medium';
    src: url(/cv-application/static/media/Montserrat-Medium.e2d60bc4.ttf) format('ttf');
}


.editor {
    background-color: rgb(232, 235, 234);
    width: 850px;
    display: flex;
    flex-direction: column;
    align-items: center;
    max-height: 1100px;
    overflow-y: auto;
    box-shadow: 0 0 10px 2px grey;
    font-family: Montserrat-Medium, sans-serif;
    font-weight: 100;
    margin-top: 40px;
    margin-bottom: 40px;
}



.personal-info {
    display: flex;
    flex-direction: column;
    grid-gap: 10px;
    gap: 10px;
    align-items: center;
    width:100%;
}

.personal-info input {
    height: 3em;
    width: 80%;
}

.education {
    display: flex;
    flex-direction: column;
    align-items: center;
    width:100%;
}

.education-input-list {
    width:100%;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}
.education-inputs {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    width:100%;
}
.education-inputs input {
    height: 3em;
    width :80%;
}


.experience {
    display: flex;
    flex-direction: column;
    grid-gap:0px;
    gap:0px;
    align-items: center;
    width:100%;
}

.experience-input-list {
    width:100%;
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
}

.experience-inputs {
    display: flex;
    flex-direction: column;
    align-items: center;
    grid-gap: 10px;
    gap: 10px;
    width:100%;
}
.experience-inputs input {
    height: 3em;
    width :80%;
}

.pdf-button {
    background-color: rgb(60, 160, 247);
    min-height: 40px;
    margin-top: 40px;
}

.clear-button {
    min-height:40px;
    margin-bottom: 30px;
}
button {
    width: 81%;
    height:40px;
    background-color: rgb(49, 46, 46);
    color:aliceblue;
    border-radius: 5px ;
    border-style: none;
    margin-top: 10px;
    font-family: Nunito, sans-serif;
    font-size: 20px;;
}

button:hover {
    opacity: .7;
}

.editor-header {
    font-size: 20px;
    width:80%;
    margin-top: 30px;
    font-family: Nunito, sans-serif;
    font-weight: 900;
}

input {
    border-style: none;
    outline-color: rgb(136, 136, 136);
    border-radius: 5px;
    padding:3px;
    padding-left: 10px;
    font-family: Nunito,sans-serif;
}

.delete-button {
    background-color: rgb(240, 61, 16);
}

.add-button {
    background-color: green;
}

@font-face {
    font-family: 'Montserrat-Medium';
    src: url(/cv-application/static/media/Montserrat-Medium.e2d60bc4.ttf) format('ttf');
}
@font-face {
    font-family: 'Montserrat-ExtraBold';
    src: url(/cv-application/static/media/Montserrat-ExtraBold.1944c8d1.ttf) format('ttf');
}

.preview {
    height: 1100px;
    width: 850px;
    background-color: rgb(238, 238, 238);
    display: flex;
    flex-direction: column;
    padding-bottom: auto;
    overflow-y: auto;
    box-shadow: 0 0 10px 2px grey;
    margin-top: 40px;
    margin-bottom: 40px;

}

.preview-header {
    background-color: rgb(34, 65, 121);
    margin-top: 0;
    min-height: 100px;
}

.preview-name {
    font-family: Montserrat-ExtraBold, sans-serif;
    font-size: 50px;
    margin-left: 20px;
    margin-bottom: 10px;
    padding: 0;
    color:rgb(255, 255, 255);
}

.preview-title {
    font-family: Montserrat-Medium, sans-serif;
    font-weight: 200;
    font-size: 30px;
    margin-top: 0;
    margin-left: 20px;
    padding: 0;
    color:rgb(255, 255, 255);

}
.preview-personal {
    margin-left: 20px;
    font-family: Montserrat-Medium, sans-serif;

}
.preview-main {
    margin-left: 20px;
    font-family: Montserrat-Medium, sans-serif;
    overflow-wrap: break-word;

}


.college-item {
    display: flex;
    justify-content: space-around;
    width:100%;
}

.date {
    width:20%;
    overflow-wrap: break-word;
}

.college-info {
    width:70%;
    overflow-wrap: break-word;
}

.college-info p {
    margin: 0;
}

.experience-item {
    display: flex;
    justify-content: space-around;
    width:100%;

}

.job-info {
    width:70%;
    overflow-wrap: break-word;
}

.preview-subtitle {
    color:rgb(34, 65, 121);
    font-size: 20px;
    padding-bottom: 0;
    margin: 0;
    border-bottom: 1px solid rgb(184, 184, 184);
    width: 90%;
    display: block;
}

.preview-personal p {
    margin: 0;
    font-size: 15px;
}

.personal-main p{ 
    font-size: 15px;
}

#personal-details, #about-me {
    margin-top: 15px;
}

#personal-details {
    margin-bottom: 10px;
}






@media print {
    body * {
      visibility: hidden;
    }
    #preview, #preview * {
      visibility: visible;
    }
    #preview {
      position: absolute;
      left: 0;
      top: 0;
      border-style: 0;
    box-shadow: none;
    }
  }

.body{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;    
}
body {
    padding:0;
    margin:0;
}
#root {
    padding:0;
    margin:0;
}

.App {
    padding:0;
    margin:0;
    background-color: rgb(235, 235, 229);

}
header {
    height: 150px;
    background-color: rgb(63, 61, 55);
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 80px;
    font-family: Arial, Helvetica, sans-serif, sans-serif;
    color:rgb(248, 246, 246);
}

 

footer {
    height:5em;
    display:flex;
    align-items: center;
    font-family: Verdana, Helvetica, sans-serif;
    justify-content: center;
}
